import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import SiteData from "../../Data/SiteData";

import HomeContainer from "../../Pages/Home/Home";
import AboutContainer from "../../Pages/About/About";
import WorkContainer from "../../Pages/Work/Work";
import ExampleContainer from "../../Pages/Example/Example";

import RestoreIcon from "@material-ui/icons/Restore";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// import Button from "@material-ui/core/Button";

import styled from "styled-components";

const NavContainer = styled.div`
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #e9e9e9;
  z-index: 999;

  ul {
    display: flex;
    height: 100%;
    align-items: center;

    li {
      flex-basis: 25%;
      max-width: 25%;

      a {
        display: block;
        font-size: 12px;
        color: #2196f3;
        font-weight: 400;

        .nav-icon {
          display: block;
          margin: 0 auto 4px;
          font-size: 16px;
        }
      }
    }
  }
`;

export default class Navigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isActive: false
    };
    this.onMouseOver = this.onMouseOver.bind(this);
    this.onMouseOut = this.onMouseOut.bind(this);
  }

  onMouseOver(elem) {
    this.setState({ isActive: !this.state.isActive });
  }
  onMouseOut(elem) {
    this.setState({ isActive: !this.state.isActive });
  }
  onClick() {
    console.log("index");
  }

  render() {
    let location = this.props.location;

    return (
      <div className="section">
        <Router>
          <div>
            <NavContainer className="nav-container">
              <ul className="nav-list">
                {SiteData.map((NavItem, index) => (
                  <li className={`nav-item nav-item-${NavItem.slug} ${index}`}>
                    <Link
                      className={`${this.state.isActive ? "is-active" : ""}`}
                      to={NavItem.dir}
                      onMouseOver={this.onMouseOver.bind(this)}
                      onMouseOut={this.onMouseOut.bind(this)}
                      onClick={this.onClick}
                    >
                      <FontAwesomeIcon
                        icon={NavItem.icon}
                        className="nav-icon"
                      />
                      {NavItem.page}
                    </Link>
                  </li>
                ))}
              </ul>
            </NavContainer>

            <Route exact path="/" component={HomeContainer} />
            <Route path="/about" component={AboutContainer} />
            <Route path="/work" component={WorkContainer} />
            <Route path="/example" component={ExampleContainer} />
          </div>
        </Router>
      </div>
    );
  }
}
