import React, { Component } from "react";
import ReactDOM from "react-dom";
import Modal from "react-modal";
import ModalStyle from "./ModalStyle.js";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    transform: "translate(-50%, -50%)",
    width: "85%",
    border: "none"
  }
};

export default class ModalContainer1 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      modalIsOpen: false
    };

    this.openModal = this.openModal.bind(this);
    // this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  openModal() {
    this.setState({ modalIsOpen: true });
  }

  // afterOpenModal() {
  //   this.subtitle.style.color = "#f00";
  // }

  closeModal() {
    this.setState({ modalIsOpen: false });
  }

  render() {
    const { num, title, text } = this.props;

    return (
      <li className="button-container">
        <ModalStyle />
        <button onClick={this.openModal} className={`modal-button-${num}`}>
          {title}
        </button>
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          style={customStyles}
          overlayClassName="modal-overlay"
          contentLabel="Example Modal"
        >
          <div className={`modal-content modal-content-${num}`}>
            <div className="modal-content-title">
              <span>{`その${num}`}</span>
              <h2>{title}</h2>
            </div>
            <div className="modal-content-read">
              <p>{text}</p>
            </div>
          </div>
        </Modal>
      </li>
    );
  }
}
