import React, { Component } from "react";
import styled from "styled-components";
import mediaQuery from "styled-media-query";

const mediaMobile = mediaQuery.lessThan("small");

const Screen = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999;

  ${mediaMobile`
    display: none;
  `};

  .pc-screen-container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    p {
      font-size: 20px;
      font-weight: 600;
    }
  }
`;

export default class PCscreen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow: true,
      windowwidth: window.innerWidth
    };
  }

  render() {
    return (
      <Screen id="pc-screen">
        <div className="pc-screen-container">
          <div className="pc-screen-read">
            <p>スマホからご覧ください。</p>
          </div>
        </div>
      </Screen>
    );
  }
}
